import React, { useState, useEffect } from "react";
import liff from "@line/liff";
import "./App.css";

function App() {
	const urlParams = new URLSearchParams(window.location.search);
	const from = urlParams.get("from") || "your from destination";
	const to = urlParams.get("to") || "your to destination";

	liff
		.init({
			liffId: "2002403726-3KKonPEQ",
		})
		.catch((error) => {
			alert(error);
			liff.closeWindow();
		});
	console.log(liff.getIDToken());

	const [arrivalTime, setArrivalTime] = useState("");

	const handleInputChange = (event) => {
		setArrivalTime(event.target.value);
	};

	const handleSubmit = (event) => {
		event.preventDefault();
		const text = "@星星-標記就出發 沒回就是沒 " + from + " " + arrivalTime;
		liff
			.sendMessages([{ type: "text", text: text }])
			.then((resp) => {
				alert(resp);
				liff.closeWindow();
			})
			.catch((error) => {
				alert(error);
				liff.closeWindow();
			});
	};

	return (
		<div className="App">
			<header className="App-header">
				<p>請問多久抵達 {from}?</p>
				<form onSubmit={handleSubmit}>
					<button type="submit">準時抵達</button>
					<label>
						抵達時間:
						<input
							type="text"
							inputMode="numeric"
							value={arrivalTime}
							onChange={handleInputChange}
							required
						/>
					</label>
					<button type="submit">送出</button>
				</form>
			</header>
		</div>
	);
}

export default App;
